/* 
//
	dimensionControl's css (margin-padding panel) starts
// 
*/

.input-wrapper {
	width: 20%;
	display: flex;
	flex-direction: column;
}

.linked-btn {
	width: 40px;
	height: 30px;
	border-radius: 5px;
	margin-left: 8px;
	padding-left: 12px;
}

.dimention-container {
	display: flex;
	flex-direction: column;
	// width: 250px; // ?
	margin-bottom: 1em;
}

.dimention-label {
	margin-bottom: 4px;
	margin-right: 100px;
}

.dimentions-input-label {
	font-size: 0.75em;
	text-align: center;
}

.input-container {
	display: flex;
	flex-direction: row;
	justify-content: space-between;

	.input-wrapper {
		&:first-child {
			input {
				border-width: 1px;
				border-radius: 4px 0px 0px 4px;
			}
		}
		input {
			min-height: 24px;
			height: 24px;
			margin: 0;
			padding: 0;
			text-align: center;
			border-width: 1px 1px 1px 0px;
			border-color: #8d96a0;
			border-style: solid;
			border-radius: 0;
			width: 100%; // Fix firefox input issue
		}
		&:nth-child(5) {
			input {
				border-radius: 0px 4px 4px 0px;
			}
		}

		button {
			margin-left: 0px;
			border-radius: 0px 4px 4px 0px;
			border: 1px solid #8d96a0;
			border-left: none;
		}
	}
}

/* 
//
	dimensionControl's css (margin-padding panel) ends
// 
*/

.eb-resButtons {
	display: flex;
	justify-content: space-between;
	padding: 5px 0;
	margin-bottom: 10px;
	gap: 5px;
}

button.eb-res-btn {
	background: #6c40f733;
	border: 1px solid #6c40f733;
	border-radius: 3px;
	padding: 5px 0;
	flex: 1;
	color: #6c40f7;
	font-weight: 600;
	letter-spacing: 0.2px;
	cursor: pointer;
	transition: 0.3s;
}

button.eb-res-btn:hover,
button.eb-res-btn.active {
	box-shadow: #6c40f799 0px 0px 5px 0px;
}

button.eb-res-btn.active {
	background: #6c40f7;
	border: 1px solid #6c40f7;
	color: #fff;
}

button.eb-res-btn:focus {
	outline: none;
}

.edit-post-visual-editor.editor-styles-wrapper {
	margin: auto;
	width: 100%;
}

// making typography component width bigger
.eb-typography-component-panel {
	min-width: 220px;
	padding: 1rem;
}

/* 
* making the edit page respond to the responsive width
*/

.editor-styles-wrapper {
	margin: auto;
	transition: 0.5s;
}

body.eb-res-option-desktop .editor-styles-wrapper {
	max-width: 100%;
}

body.eb-res-option-tab .editor-styles-wrapper {
	max-width: 768px;
}

body.eb-res-option-mobile .editor-styles-wrapper {
	max-width: 480px;
}

//
//
// typography component styles
.typoResButton {
	cursor: pointer;
	font-size: 14px;
	line-height: 23px;
	width: 16px;
}

.typoResButton.active {
	color: #6c40f7;
}

.wrap_res {
	position: relative;
}

.resIcons {
	position: absolute;
	left: 80px;
}

.eb-typography-component-panel .wrap_res.forFontSize .resIcons {
	left: 70px;
}
.eb-typography-component-panel .wrap_res.forLetterSpacing .resIcons {
	left: 100px;
}
.eb-typography-component-panel .wrap_res.forLineHeight .resIcons {
	left: 83px;
}

//
// unit control styles (which is also used inside typography control)
.eb-unit-control-btn-group {
	margin: 0px 0px -20px 0px;
	text-align: right;
	display: block;
}

.eb-unit-control-btn {
	color: black;
	background: transparent !important;
	outline: none !important;
	box-shadow: none !important;
	// -webkit-box-shadow: none !important;
	border: none !important;
	text-shadow: none !important;
}

.eb-unit-active {
	color: #6c40f7 !important;
	font-weight: bold;
}

// color control styles start
.eb-color-base div {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.eb-color-base .components-base-control__label {
	flex: 10;
	margin-right: 0px !important; /* There was no other way */
}

.eb-color-base .components-dropdown {
	flex: 1;
}

.eb-color-ball {
	background-image: linear-gradient(45deg, #ddd 25%, transparent 0),
		linear-gradient(-45deg, #ddd 25%, transparent 0),
		linear-gradient(45deg, transparent 75%, #ddd 0),
		linear-gradient(-45deg, transparent 75%, #ddd 0);
	background-size: 10px 10px;
	background-position: 0 0, 0 5px, 5px -5px, -5px 0;
	border-radius: 50%;
}

.eb-color-undo {
	flex: 1;
	color: gray;
}

.eb-color-undo :focus,
.eb-color-undo :hover {
	box-shadow: none;
}

// color control styles end

